<template>
  <div class="container">
    <div class="mutual" v-if="praiseList && praiseList.length > 0" v-for="(praise, index) in praiseList" :key="index">
      <div>
        <a>
          <img :src="praise.fabulouPic" alt="点赞用户头像" @click="enterPerCenter(praise)">
          <span>{{praise.fabulouName}}</span>
        </a>
      </div>
      <div>{{praise.fabulouDate | dateFm}}</div>
    </div>
    <div class="comment_mutual_more" v-if="praiseTotal > 5">
      <button @click="loadingMore">{{loadingMorePraise}}</button>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        page: 1,
        praiseList: new Array(),
        baseUrl: 'http://192.168.124.4:8081',
        praiseTotal: 0,
        loadingMorePraise: '加载更多'
      }
    },
    created() {
      this.loadingPraiseList()
    },
    methods: {
      loadingPraiseList() {
        this.showLoading = 1
        this.axios.get('/api/getWorksFabulouList', {
          params: {
            worksId: this.$route.params.id,
            page: this.page
          }
        }).then(data => {
          this.praiseTotal = data.data.result.total
          this.showLoading = 0
          if (data.data.result.list.length > 0) {
            this.loadingMorePraise = '加载更多'
            data.data.result.list.forEach(element => {
              if (element.fabulouPic.indexOf('http') == -1) {
                element.fabulouPic = this.baseUrl + element.fabulouPic
              }
            })
            if(this.praiseList.length > 0){
              this.praiseList = this.praiseList.concat(data.data.result.list)
            }else{
              this.praiseList = data.data.result.list
            }
          } else {
            this.$Toast({
              message: '没有更多信息了',
              position: 'middle',
              duration: 1000
            })
            this.loadingMorePraise = '没有更多信息了'
          }
        }).catch(err => {
          console.log(err)
        })
      },
      // 加载更多
      loadingMore() {
        this.page++
        this.loadingPraiseList()
      },
      // 进入个人中心
      enterPerCenter(userInfo) {
        this.$router.push({
          name: 'perWorkCircle',
          params: {
            id: userInfo.fuserId
          }
        })
      },
    }
  }
</script>


<style lang="less" scoped>
  .comment_mutual_more {
    text-align: center;
    margin-bottom: 3.75rem;
    height: 1.875rem;
    line-height: 1.875rem;
    &>button {
      background-color: #fff;
      border: none;
      border: 0;
      outline: none;
      color: #000;
    }
  }
  .mutual {
    display: flex;
    box-sizing: border-box;
    background-color: #fff;
    div {
      box-sizing: border-box;
    }
    div:nth-child(1) {
      padding: .625rem 0 0 .625rem;
      flex: 7;
      a {
        img {
          width: 3.125rem;
          height: 3.125rem;
          border-radius: 50%;
          vertical-align: middle;
        }
        span {
          display: inline-block;
          vertical-align: middle;
          color: #3fb140;
          margin-left: .3125rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          width: 9.375rem;
        }
      }
    }
    div:nth-child(2) {
      color: #aaa;
      text-align: right;
      padding: 1.875rem 1.25rem 0 0;
      flex: 3;
    }
  } // 底部评论和点赞 END
  .container {
    margin-bottom: 4.375rem;
  }
</style>



